// фонове зображення

.header--library {
  height: 230px;
  background-color: rgba(0, 0, 0, 0.56);
  background-image: linear-gradient(
      to right,
      rgba(0, 0, 0, 0.56),
      rgba(0, 0, 0, 0.56)
    ),
    url(../images/header-watched/watched-mobile@1x.jpg);

  @media (min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
    background-image: linear-gradient(
        to right,
        rgba(0, 0, 0, 0.56),
        rgba(0, 0, 0, 0.56)
      ),
      url(../images/header-watched/watched-mobile@2x.jpg);
  }

  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;

  max-width: 1600px;
  margin-left: auto;
  margin-right: auto;

  & {
    @media screen and (min-width: 768px) {
      height: 216px;
      background-image: linear-gradient(
          to right,
          rgba(0, 0, 0, 0.56),
          rgba(0, 0, 0, 0.56)
        ),
        url(../images/header-watched/watched-tablet@1x.jpg);
    }

    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: linear-gradient(
          to right,
          rgba(0, 0, 0, 0.56),
          rgba(0, 0, 0, 0.56)
        ),
        url(../images/header-watched/watched-tablet@2x.jpg);
    }
  }

  & {
    @media screen and (min-width: 1280px) {
      background-image: linear-gradient(
          to right,
          rgba(0, 0, 0, 0.56),
          rgba(0, 0, 0, 0.56)
        ),
        url(../images/header-watched/wathed-laptop@1x.jpg);

      @media (min-device-pixel-ratio: 2),
        (min-resolution: 192dpi),
        (min-resolution: 2dppx) {
        background-image: linear-gradient(
            to right,
            rgba(0, 0, 0, 0.56),
            rgba(0, 0, 0, 0.56)
          ),
          url(../images/header-watched/watched-laptop@2x.jpg);
      }
    }
  }
}

.button__box {
  display: flex;
  justify-content: center;
  margin-top: 60px;
  gap: 20px;

  & {
    @media screen and (min-width: 768px) {
      margin-top: 40px;
      gap: 32px;
    }
  }
}

.button__library {
  width: 130px;
  height: 44px;
  cursor: pointer;
  background-color: transparent;
  border: 1px solid #ffffff;
  border-radius: 5px;

  font-size: 12px;
  line-height: 1.3;

  text-transform: uppercase;

  color: #ffffff;

  transition-duration: 200ms;

  & {
    @media screen and (min-width: 768px) {
      width: 150px;
    }
  }

  &:hover,
  &:focus {
    border: 1px solid #ff6b01;
    background-color: #ff6b01;
    box-shadow: 0px 8px 43px rgba(255, 107, 1, 0.6);

    animation-name: button;
    animation: button 500ms ease 0s 0.5 normal none;

    @keyframes button {
      0% {
        transform: translate(0);
      }

      10% {
        transform: translate(-2px, -2px);
      }

      20% {
        transform: translate(2px, -2px);
      }

      30% {
        transform: translate(-2px, 2px);
      }

      40% {
        transform: translate(2px, 2px);
      }

      50% {
        transform: translate(-2px, -2px);
      }

      60% {
        transform: translate(2px, -2px);
      }

      70% {
        transform: translate(-2px, 2px);
      }

      80% {
        transform: translate(-2px, -2px);
      }

      90% {
        transform: translate(2px, -2px);
      }

      100% {
        transform: translate(0);
      }
    }
  }
}
